import React, { Profiler, useContext, useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Nav } from 'react-bootstrap';
import './Detail.scss';
import { CSSTransition } from 'react-transition-group';
import { connect } from 'react-redux';
import { stockContext } from './App.js';

function Detail(props) {
	let [alert, setAlert] = useState(true);
	let [input, setInput] = useState('');
	let [quan, setQuan] = useState(1);
	let stock = useContext(stockContext);

	let [tap, setTap] = useState(0);
	let [onOff, setOnOff] = useState(false);

	let { id } = useParams();
	let history = useHistory();
	let product2 = localStorage.getItem('product');
	product2 = JSON.parse(product2);
	let product = product2.find((p) => {
		return p.id == id;
	});
	useEffect(() => {
		var arr = localStorage.getItem('watched');
		if (arr === null) {
			arr = [];
		} else {
			arr = JSON.parse(arr);
		}
		arr.unshift(id);
		arr = new Set(arr);
		arr = [...arr];
		localStorage.setItem('watched', JSON.stringify(arr));
	}, []);

	useEffect(() => {
		let timer = setTimeout(() => {
			setAlert(false);
		}, 2000);
		if (props.stock[0] > 3) {
			setAlert(false);
		}
		return () => {
			clearTimeout(timer);
		};
	}, []);

	function StorageSave() {
		let arr = localStorage.getItem('cart');
		if (arr == null) {
			arr = [];
		} else {
			arr = JSON.parse(arr);
		}
		let found = arr.findIndex((a) => {
				return a.id === product.id;
			}
		);
		if ( found >= 0) {
			arr[found].quan++;
		} else {
			arr.push({
				id: product.id, 
				name: product.title,
				quan: quan,
				price: product.price,
			});
		}
		localStorage.setItem('cart', JSON.stringify(arr));
	}

	return (
		<div className='container'>
			<button
				className='btn btn-light'
				onClick={() => {
					history.goBack();
				}}
			>
				뒤로가기
			</button>
			<div className='navTitle'>
				<h1>Detail</h1>
			</div>
			{alert === true ? <Alert></Alert> : null}
			<div className='row'>
				<div className='col-md-6'>
					<img
						src={
							'https://codingapple1.github.io/shop/shoes' +
							(product.id + 1) +
							'.jpg'
						}
					/>
				</div>
				<div className='col-md-6 mt-1'>
					<h2 className=''>{product.title}</h2>
					<p>{product.content}</p>
					<p>{product.price}원</p>
					<p>재고 : {props.stock[product.id]}</p>

					<div>
						<p>
							<span>수량 : </span>
							<button
								onClick={() => {
									if (props.stock[product.id] > quan) {
										setQuan(quan + 1);
									}
								}}
							>
								+
							</button>
							<span> {quan} </span>
							<button onClick={() => {
								if (quan > 1) {
									setQuan(quan - 1);
								}
								}}>-</button>
						</p>
					</div>

					<button
						className='btn btn-danger'
						onClick={() => {
							StorageSave();
							props.dispatch({
								type: 'product_add',
								data: {
									id: product.id,
									name: product.title,
									quan: quan,
									price: product.price,
								},
							});
							history.push('/cart');
						}}
					>
						주문하기
					</button>
				</div>
			</div>

			<Nav className='mt-5' variant='pills' defaultActiveKey='/home'>
				<Nav.Item className=''>
					<Nav.Link
						className=''
						eventKey='link-0'
						onClick={() => {
							setOnOff(false);
							setTap(0);
						}}
					>
						최근본상품
					</Nav.Link>
				</Nav.Item>
				<Nav.Item className=''>
					<Nav.Link
						className=''
						eventKey='link-1'
						onClick={() => {
							setOnOff(false);
							setTap(1);
						}}
					>
						배송정보
					</Nav.Link>
				</Nav.Item>
				<Nav.Item className=''>
					<Nav.Link
						className=''
						eventKey='link-2'
						onClick={() => {
							setOnOff(false);
							setTap(2);
						}}
					>
						상품설명
					</Nav.Link>
				</Nav.Item>
			</Nav>

			<CSSTransition in={onOff} classNames='wow' timeout={500}>
				<TabContent shoes={props.shoes} tap={tap} setOnOff={setOnOff} />
			</CSSTransition>
		</div>
	);
}

function TabContent(props) {
	useEffect(() => {
		props.setOnOff(true);
	});
	if (props.tap === 0) {
		return <RecentlyViewedItems shoes={props.shoes}></RecentlyViewedItems>;
	} else if (props.tap === 1) {
		return (
			<div>
				Lorem ipsum dolor sit amet, consectetur adipisicing elit. Voluptatem
				alias, accusamus praesentium vel voluptas, quidem dolorum corporis animi
				dicta inventore autem est eveniet necessitatibus iste fugit hic,
				eligendi quas vero!
			</div>
		);
	} else if (props.tap === 2) {
		return (
			<div>
				Lorem ipsum dolor sit amet consectetur adipisicing elit. Aperiam vitae,
				beatae, culpa quasi ducimus reiciendis reprehenderit ipsum laborum sequi
				repellendus id. Dolore tenetur minus, consequatur alias non voluptatum
				temporibus itaque?
			</div>
		);
	}
}

function RecentlyViewedItems(props) {
	let arr = localStorage.getItem('watched');
	arr = JSON.parse(arr);
	let product = localStorage.getItem('product');
	product = JSON.parse(product);
	let history = useHistory();
	return arr.map((a) => {
		return (
			<div
				className='col-md-4 recent'
				onClick={() => {
					history.push('/detail/' + product[a].id);
					window.scrollTo(0, 0);
					console.log(product[a].id);
				}}
			>
				<img
					className='recentImg'
					src={
						'https://codingapple1.github.io/shop/shoes' +
						(parseInt(a) + 1) +
						'.jpg'
					}
					alt=''
				/>
				<h2>{product[a].title}</h2>
				<p>{product[a].price}</p>
			</div>
		);
	});
}

function Alert() {
	return (
		<div className='my-alert'>
			<p>재고가 얼마 남지 않았습니다</p>
		</div>
	);
}

function state를props화(state) {
	return {
		state: state.reducer,
		alert: state.reducer2,
	};
}

export default connect(state를props화)(Detail);
